import { Link } from 'react-router-dom';
import Tippy from '@tippyjs/react/headless';
import classNames from 'classnames/bind'

import Cart from '../../components/Cart';
import { Basket } from '../../components/Icons';
import styles from './index.module.scss'

const cx = classNames.bind(styles)

function ActionBar() {
    return (
        <div className={cx("wrapper")}>
            <div className={cx('account')}>
                <img className={cx('image')} src="https://didongviet.vn/dchannel/wp-content/uploads/2022/01/cute-didongviet.jpg" alt="nh" />
                <div className={cx('left')}>
                    <div className={cx('name')}>Thi Phng Nam</div>
                    <div className={cx('nickname')}>@namthaphu1071</div>
                </div>
            </div>
            <Tippy
                placement='top'
                interactive
                delay={[0, 250]}
                offset={[0, -4]}
                render={(attrs) => (
                    <div tabIndex="-1" {...attrs}>
                        <Cart />
                    </div>
                )}
            >
                <Link to="/gio-hang" className={cx('cart')}>
                    <Basket height="1.5rem" width="1.5rem" className={cx('icon')} />
                    <span>Gi hng</span>
                    <div className={cx('quantity')}>+44</div>
                </Link>
            </Tippy>
        </div >
    );
}

export default ActionBar;