import classNames from 'classnames/bind'

import CategoryItem from './CategoryItem'
import styles from './index.module.scss'

const cx = classNames.bind(styles)

function Category({ data }) {
    const category1 = {
        id: 1,
        name: 'Danh mc 1',
        image: {
            alt: 'nh danh mc',
            src: 'https://tronhouse.com/assets/data/editor/source/meo-chup-anh-san-pham-quan-ao-de-kinh-doanh-online-hieu-qua/chup-anh-quan-ao-3.jpg'
        }
    }
    const category2 = {
        id: 2,
        name: 'Danh mc 2',
        image: {
            alt: 'nh danh mc',
            src: 'https://tronhouse.com/assets/data/editor/source/meo-chup-anh-san-pham-quan-ao-de-kinh-doanh-online-hieu-qua/chup-anh-quan-ao-3.jpg'
        }
    }
    const category3 = {
        id: 3,
        name: 'Danh mc 3',
        image: {
            alt: 'nh danh mc',
            src: 'https://tronhouse.com/assets/data/editor/source/meo-chup-anh-san-pham-quan-ao-de-kinh-doanh-online-hieu-qua/chup-anh-quan-ao-3.jpg'
        }
    }

    return (
        <div className={cx('wrapper')}>
            <div className={cx('title')}>Danh mc</div>
            <div className={cx('list')}>
                <CategoryItem className={cx('item')} data={category1} />
                <CategoryItem className={cx('item')} data={category2} />
                <CategoryItem className={cx('item')} data={category3} />
            </div>
        </div>
    );
}

export default Category;