import Tippy from '@tippyjs/react/headless';
import { Link } from 'react-router-dom';
import classNames from 'classnames/bind'

import TagSalesOff from './TagSalesOff';
import styles from './index.module.scss'

const cx = classNames.bind(styles)

function ProductItem({ data }) {
    const to = '/san-pham/' + data.id
    return (

        // Using a wrapper <div> or <span> tag around the reference element solves this by creating a new parentNode context.
        <div>
            <Tippy
                placement="bottom"
                offset={[0, -16]}
                interactive
                render={(attrs) => (
                    <div tabIndex="-1" {...attrs}>
                        <div className={cx('action')}>
                            <button className={cx('add-cart')}>Thm vo gi hng</button>
                        </div>
                    </div>
                )}
            >
                <div className={cx('wrapper')} >
                    <TagSalesOff data={data.salesOff} />
                    <Link to={to}><img className={cx("image")} src={data.image.src} alt={data.image.alt} /></Link>
                    <div className={cx("price")}>
                        
                        <div className={cx('default')}>{data.price.default}</div>
                        &#8594;
                        
                        <div className={cx('salesOff')}>{data.price.salesOff}</div>
                    </div>
                    <Tippy
                        placement="top"
                        offset={[0, 38]}
                        interactive
                        render={(attrs) => (
                            <div tabIndex="-1" {...attrs}>
                                <div className={cx('name')}>{data.name}</div>
                            </div >
                        )}
                    >
                        <div className={cx('name-short')}>{data.name}</div>
                    </Tippy >
                </div>
            </Tippy>
        </div>
    );
}

export default ProductItem;